import uniRate from '@/components/uni-rate/uni-rate.vue'
import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'
import sku from '@/components/sku/pages/sku.vue'
import sPopup from '@/components/s-popup/index';
// import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default{
	components:{
		uniRate,
		uniGoodsNav,
		sku,
		sPopup
		// uniPopup
	},
	onShow(){
		
	},
	data () {
	      return {
			  videoContext:'',
			  image:'',
			  advImg:'',
			  hideNav:true,
			  autoplay:true,
			  path:'',
			  logined:false,
			  popshow:false,
			  visible:false,
			  uid:'',
			  token:'',
			  id:'',
			  merchId:'',
			  data:'',
			  thumb_url:'',
			  category:[],
			  total:'',
			  receivedCategory:false,
			  needCategory:false,
			  choosedSpec:'',
	        options: [{
	          icon: 'icon-kefu',
	          text: '客服'
	        }, {
	          icon: 'icon-iconfontshop-copy',
	          text: '店铺'
	        }, {
	          icon: 'icon-star',
	          text: '收藏',
			  collected:false,
			  color:'ff9c00'
	          // info: 0
	        }],
	        buttonGroup: [{
	          text: '加入购物车',
	          backgroundColor: '#ff0000',
	          color: '#fff'
	        },
	        {
	          text: '立即抢购',
	          backgroundColor: '#ffa200',
	          color: '#fff'
	        }
	        ],
			// 分享
			providerList:[],
			shareType:0,
			shareText:'嘎哈'
	      }
	    },
		onLoad(p){
			var that=this
			this.id=p.id
			// this.merchId=p.merchId
			var userInfo=uni.getStorageSync('userInfo')
			if(userInfo!='' & userInfo!=null & userInfo!=undefined){
				this.logined=true
				this.uid=userInfo.uid
				this.token=userInfo.token
			}else{
				this.logined=false
			}
			this.getDetail()
			this.getProvider()
			uni.$on('logined',function(){
				var userInfo2=uni.getStorageSync('userInfo')
				that.logined=true
				that.uid=userInfo2.uid
				that.token=userInfo2.token
				that.getDetail()
			})
		},
		onShareAppMessage() {
			return {
				title: "小美女小帅哥快来买啊",
				path: '/pages/index/goodsDetail?id='+this.id,
				imageUrl:this.image ? this.image : 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/app/share-logo@3.png'
			}
		},
		onPageScroll(e){
			if(e.scrollTop==0){
				this.hideNav=true
			}else{
				this.hideNav=false
			}
		},
	    methods: {
			toGoodsDetail(goodsid,merchid){
				uni.navigateTo({
					url:'goodsDetail?id='+goodsid+'&merchId='+merchid
				})
			},
	      onClick (e) {
			  if(e.index==0){
				  // 客服
			  }else if(e.index==1){
				  // 店铺
				  this.toShop(this.data.merchid)
			  }else if(e.index==2){
				  // 收藏
				  this.collect(e.content.collected)
			  }
	        // uni.showToast({
	        //   title: `点击${e.content.text}`,
	        //   icon: 'none'
	        // })
	      },
	      buttonClick (e) {
	        console.log(e)
			if(e.index==0){
				this.addCollect()
			}else{
				this.buy()
			}
	        // this.options[2].info++
	      },
		  // 添加购物车
		  addCollect(){
			  var ce=this.$operateInterceptor(this.logined)
			  if(!ce){
			  	return ;
			  }
			  var that=this
			  var params={
			    uid:this.uid,
			    token: this.token,
				goodsid:this.id
			  }
			    var url='&r=api.member.cart.add'
			    this.$apiPost(url,params).then((res) =>{
			  		// that.options[2].info++
					that.$msg('添加成功')
			    })
		  },
		  // 购买
		  buy(){
			  var ce=this.$operateInterceptor(this.logined)
			  if(!ce){
			  	return ;
			  }
			  if((this.choosedSpec.choosedid=='' || this.choosedSpec.choosedid==undefined || this.choosedSpec.choosedid==null) & this.needCategory){
			  	this.chooseCategory()
			  	return ;
			  }
			  if(this.total==null || this.total==0){
				  this.$msg('该商品已卖完')
				  return ;
			  }
			  uni.navigateTo({
			  	url:'./createOrder?goodsId='+this.id+'&merchId='+this.data.merchid
				+'&choosedSpec='+JSON.stringify(this.choosedSpec)+'&goodsName='+this.data.title+'&total='+this.total+'&marketprice='+this.data.marketprice
			  })
		  },
		  // 购物车数量
		  getCollectNum(){
			  
		  },
		  // 更改收藏状态
		  collect(colected){
			  var ce=this.$operateInterceptor(this.logined)
			  if(!ce){
			  	return ;
			  }
			  var that=this,url
			  var params={
			    uid:this.uid,
			    token: this.token,
			  					goodsid:this.id
			  }
			  if(colected){
				   url='&r=api.member.favorite.remove'
			  }else{
				   url='&r=api.member.favorite.add'
			  }
			    this.$apiPost(url,params).then((res) =>{
			  		// that.options[2].info++
					if(colected){
						that.options[2].collected=false
						that.options[2].icon='icon-star'
					}else{
						that.options[2].collected=true
						that.options[2].icon='icon-shoucang'
					}
			    })
		  },
		  // 关注
		  focus(id){
			  var ce=this.$operateInterceptor(this.logined)
			  if(!ce){
			  	return ;
			  }
			  if(this.data.isCollect=='1'){
				  return ;
			  }
			  var that=this
			  var params={
			    uid:this.uid,
			    token: this.token,
			  	merchid:id
			  }
			    var url='&r=api.member.collection.add'
			    this.$apiPost(url,params).then((res) =>{
			  		// that.options[2].info++
					that.data.isCollect='1'
			  						that.$msg('已关注')
			    })
		  },
		  chooseCategory(){
			  if(this.needCategory){
				  this.$refs.sku.specClass='show'
			  }
		  },
		  openShare(){
			  this.popshow=true
		  },
		  getDetail(){
			  var that=this
			  var url='&r=api.goods.detail&id='+this.id+'&uid='+this.uid+'&token='+this.token
			  // var params={
			  //   page:p,
			  //   pagesize: this.pageSize
			  // }
			    // var url='/wangtosale_list'
			    this.$apiPost(url).then((res) =>{
					that.data=res.data
					that.thumb_url=res.data.thumb_url
					if(that.data.isFavorite=='1'){
							that.options[2].collected=true
							that.options[2].icon='icon-shoucang'
					}else{
						that.options[2].collected=false
						that.options[2].icon='icon-star'
					}
					that.$nextTick(function(){
						that.getCategory()
						that.videoContext = uni.createVideoContext('myVideo')
					})
			  	  // that.total=res.total
			  	  // that.dataList=that.dataList.concat(res.data)
			  	  // that.more=''
			    })
		  },
		  getCategory(){
			  var that=this
			  var url='&r=api.goods.detail.sku&goodsid='+this.id
			    this.$apiPost(url).then((res) =>{
						that.category=res.data
						that.total=res.stock
						if(res.resultMessage=='暂无数据'){
							that.needCategory=false
						}else{
							that.needCategory=true
						}
						that.$nextTick(function(){
							that.receivedCategory=true
						})
			    })
		  },
		  pause(){
			this.autoplay=true
			},
			play(){
			this.autoplay=false
			},
			swiperChange(e){
				this.autoplay=true
				this.videoContext.pause();
			},
		  completeSpecChoose(e){
			  this.choosedSpec=e
		  },
		  toComments(){
			  if(!this.data.comments){
				  return ;
			  }
			  uni.navigateTo({
			  	url:'./goodsComments?id='+this.id
			  })
		  },
		  toShop(id){
		  	uni.navigateTo({
		  		url:`/pages/bussiness/shopPreview?id=${id}`
		  	})
		  },
		  // 海报
		  createPoster(){
			  	var ce=this.$operateInterceptor(this.logined)
			  	if(!ce){
			  		return ;
			  	}
				this.$loading()
			  	var that=this
			  	var params={
			  		uid:this.uid,
			  		token:this.token,
					goodsid:this.id
			  	}
			  	var url='&r=api.common.share.createPoster'
			  	  this.$apiPost(url,params).then((res) =>{
			  		  that.advImg=res.data.img
			  		  that.visible=true
					  that.popshow=false
					  uni.hideLoading()
			  	  })
		  },
		  saveImg(url){
		  	var that=this
		  	that.$loading()
		  	uni.downloadFile({
		  			url: url,
		  			success: (res) =>{
		  				if (res.statusCode === 200){
		  					uni.saveImageToPhotosAlbum({
		  						filePath: res.tempFilePath,
		  						success: function() {
		  							that.$msg('保存成功，请到相册中查看')
		  							that.visible=false
		  						},
		  						fail: function() {
		  							that.$msg('保存失败')
		  						},
		  						complete:() =>{
		  							uni.hideLoading()
		  						}
		  					});
		  				}
		  			}
		  		})
		  },
		  // 分享
		  getProvider(){
			  uni.getProvider({
			  	service: 'share',
			  	success: (e) => {
			  		console.log('success', e);
			  		let data = []
			  		for (let i = 0; i < e.provider.length; i++) {
			  			switch (e.provider[i]) {
			  				case 'weixin':
			  					data.push({
			  						name: '微信',
			  						id: 'weixin',
			  						sort:0
			  					})
			  					data.push({
			  						name: '朋友圈',
			  						id: 'weixin',
			  						type:'WXSenceTimeline',
			  						sort:1
			  					})
			  					break;
			  				// case 'sinaweibo':
			  				// 	data.push({
			  				// 		name: '分享到新浪微博',
			  				// 		id: 'sinaweibo',
			  				// 		sort:2
			  				// 	})
			  				// 	break;
			  				// case 'qq':
			  				// 	data.push({
			  				// 		name: '分享到QQ',
			  				// 		id: 'qq',
			  				// 		sort:3
			  				// 	})
			  				// 	break;
			  				default:
			  					break;
			  			}
			  		}
			  		this.providerList = data.sort((x,y) => {
			  			return x.sort - y.sort
			  		});
			  	},
			  	fail: (e) => {
			  		console.log('获取分享通道失败', e);
			  		uni.showModal({
			  			content:'获取分享通道失败',
			  			showCancel:false
			  		})
			  	}
			  });
		  },
		  async share(e) {
			  console.log(e)
		  	console.log('分享通道:'+ e.id +'； 分享类型:' + this.shareType);
		  	
		  	// if(!this.shareText && (this.shareType === 1 || this.shareType === 0)){
		  	// 	uni.showModal({
		  	// 		content:'分享内容不能为空',
		  	// 		showCancel:false
		  	// 	})
		  	// 	return;
		  	// }
		  	
		  	// if(!this.image && (this.shareType === 2 || this.shareType === 0)){
		  	// 	uni.showModal({
		  	// 		content:'分享图片不能为空',
		  	// 		showCancel:false
		  	// 	})
		  	// 	return;
		  	// }
		  	
		  	let shareOPtions = {
		  		provider: e.id,
				extra: {
					scene: "WXSceneSession"
				},
		  		scene: e.type && e.type === 'WXSenceTimeline' ? 'WXSenceTimeline' : 'WXSceneSession', //WXSceneSession”分享到聊天界面，“WXSenceTimeline”分享到朋友圈，“WXSceneFavorite”分享到微信收藏     
		  		type: this.shareType,
		  		success: (e) => {
		  			console.log('success', e);
		  			uni.showModal({
		  				content: '已分享',
		  				showCancel:false
		  			})
		  		},
		  		fail: (e) => {
		  			console.log('fail', e)
		  			uni.showModal({
		  				content: e.errMsg,
		  				showCancel:false
		  			})
		  		},
		  		complete:function(){
		  			console.log('分享操作结束!')
		  		}
		  	}
		  	
		  	switch (this.shareType){
		  		case 0:
		  			shareOPtions.summary = '好玩';
		  			shareOPtions.imageUrl = 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/app/share-logo@3.png';
		  			shareOPtions.title = this.data.title;
		  			shareOPtions.href = 'https://uniapp.dcloud.io';
		  			break;
		  		case 1:
		  			shareOPtions.summary = this.shareText;
		  			break;
		  		case 2:
		  			shareOPtions.imageUrl = this.image;
		  			break;
		  		case 5:
		  			shareOPtions.imageUrl = this.image ? this.image : 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/app/share-logo@3.png'
		  			shareOPtions.title = this.data.title+'买啊';
		  			shareOPtions.miniProgram = {
		  				id:'gh_33446d7f7a26',
		  				path:'/pages/tabBar/component/component',
		  				webUrl:'https://uniapp.dcloud.io',
		  				type:0
		  			};
		  			break;
		  		default:
		  			break;
		  	}
		  	
		  	if(shareOPtions.type === 0 && plus.os.name === 'iOS'){//如果是图文分享，且是ios平台，则压缩图片 
		  		shareOPtions.imageUrl = await this.compress();
		  	}
		  	if(shareOPtions.type === 1 && shareOPtions.provider === 'qq'){//如果是分享文字到qq，则必须加上href和title
		  		shareOPtions.href = 'https://uniapp.dcloud.io';
		  		shareOPtions.title = '欢迎体验uniapp';
		  	}
		  	uni.share(shareOPtions);
		  },
	    }
}
